import {Injectable} from '@angular/core';

@Injectable()
export class VariableService {

  gcodePrintingFileDirectory = 'some/default/directory';
  gcodeUnfinishedLine = 0;
  currentPrintTime = 0;


  constructor() {}

  /**
   * standardizes time to show
   */
  stringizedTime(t) {
    let days, hours, minutes, seconds;
    days = Math.floor(t / 86400);
    t -= days * 86400;
    hours = Math.floor(t / 3600) % 24;
    t -= hours * 3600;
    minutes = Math.floor(t / 60) % 60;
    t -= minutes * 60;
    seconds = t % 60;

    return [
      days,
      'd ',
      hours,
      'h ',
      minutes,
      'm',
    ].join('');
  }

}



