import {Component, OnInit} from '@angular/core';
import {retry} from 'rxjs/operators';
import {HttpService} from '../services/http.service';
import {ServerMatch} from '../shared/servermatch';
import {Router} from '@angular/router';
import {DataService} from '../services/data.service';
import {ConfirmComponent} from '../shared/confirm/confirm.component';
import {MatDialog, MatSnackBar} from '@angular/material';
import {DialogUsage} from '../shared/enum/dialogUsage';
import {HeatComponent} from '../shared/heat/heat.component';
import {FanComponent} from '../shared/fan/fan.component';
import {ProgressService} from '../services/progress.service';
import {VariableService} from '../services/variable.service';

enum PrintStatus {
  print = 'print',
  resume = 'resume',
  pause = 'pause',
  stop = 'stop',
}

@Component({
  selector: 'app-print-page',
  templateUrl: './print-page.component.html',
  styleUrls: ['./print-page.component.css']
})
export class PrintPageComponent implements OnInit {
  pageName = 'print-page';

  printPercent = 0;
  zPosition = 0;

  temps = {
    bed: {
      cur: 0,
      goal: 10,
    },
    ext: {
      cur: 0,
      goal: 10,
    }
  };

  elements: any;

  printTime;
  printingStatus: PrintStatus = PrintStatus.print;

  isOnHeatPage = false;
  isOnSpeedPage = false;
  isOnOptionsPage = false;
  isOnFanPage = false;

  filamentInterval;
  isFilamentFinished = false;


  constructor(private httpService: HttpService, private router: Router,
    public dataService: DataService, private dialog: MatDialog,
    private progressService: ProgressService, private snackBar: MatSnackBar,
    public variableService: VariableService) {
    this.elements = {
      resume: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png',
      },
      pause: {
        imageUrl: ServerMatch.STATIC + 'assets/images/down.png',
      },
      stop: {
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
      heat: {
        title: 'Heat',
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png',
      },
      speed: {
        title: 'Speed',
        imageUrl: ServerMatch.STATIC + 'assets/images/speed.png',
      },
      fan: {
        title: 'Fans',
        imageUrl: ServerMatch.STATIC + 'assets/images/fans.png',
        child: {
          offImageUrl: ServerMatch.STATIC + 'assets/images/fans.png',
          halfImageUrl: ServerMatch.STATIC + 'assets/images/fans.png',
          fullImageUrl: ServerMatch.STATIC + 'assets/images/fans.png',
        }
      },
      options: {
        title: 'Options',
        imageUrl: ServerMatch.STATIC + 'assets/images/options.png',
      },
      closeOptions: {
        title: 'بازگشت',
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      }
    }
  }

  ngOnInit() {
    this.dataService.temps.subscribe(data => {
      this.temps.bed = data.bed;
      this.temps.ext = data.ext;
    });
    this.dataService.percentage.subscribe(data => {
      this.printPercent = data;

      if (this.printPercent === 100) {
        this.onFinishPrinting();
      }
    });
    this.dataService.printTime.subscribe(data => {
      this.printTime = this.variableService.stringizedTime(data);
    });
    this.dataService.zPosition.subscribe(data => {
      this.zPosition = data;
    });
    this.checkFilamentFinish();
  }

  checkFilamentFinish() {
    this.filamentInterval = setInterval(() => {
      this.httpService.get('filament', true).subscribe(
        data => {
          if (data['filament_flag']) { // means that we need to change the filament
            clearInterval(this.filamentInterval);
            const rmDialog = this.dialog.open(ConfirmComponent, {
              width: '500px',
              data: {
                usage: DialogUsage.FilamentFinished,
              }
            });
            rmDialog.afterClosed().subscribe(status => {
              this.printingStatus = PrintStatus.pause;
            });
          }
        },
        err => {
          console.warn('could not get filament status: ', err);
        }
      );
    }, 10000);
  }

  onFinishPrinting() {
    // sending print data to server -> will be done there :D
    // this.httpService.post('', {
    //   // print infos
    // }).pipe(retry(3)).subscribe(
    //   data => {
    //   },
    //   err => {
    //   }
    // );

    this.dataService.changeInterval('printTime', false);
    this.dataService.changeInterval('percentage', false);
    const rmDialog = this.dialog.open(ConfirmComponent, {
      width: '500px',
      data: {
        usage: DialogUsage.FinishPrinting,
      }
    });
    rmDialog.afterClosed().subscribe(status => {
      // if (status) {
      this.dataService.setUpBasic('percentage');
      this.dataService.setUpBasic('printTime');
      this.goBackToHomePage();
      // }
    });
  }

  beforePrint(action = 'percentage') {
    this.dataService.lockAuthorize().then(res => {
      if (action === 'stop') {
        const rmDialog = this.dialog.open(ConfirmComponent, {
          width: '450px',
          data: {
            usage: DialogUsage.StopPrinting,
          }
        });
        rmDialog.afterClosed().subscribe(
          status => {
            if (status) {
              this.printAction(action);
            }
          });
      } else {
        this.printAction(action);
      }
    }, err => {});
  }

  printAction(action = 'percentage') {
    this.httpService.post(`print`, {action}, true).subscribe(
      data => {
        if (data['status'] === 'success') {
          this.printingStatus = PrintStatus[action];

          if (this.printingStatus === PrintStatus.stop) {
            this.dataService.setUpBasic('percentage');
            this.dataService.setUpBasic('printTime');
            this.dataService.changeInterval('printTime', false);
            this.goBackToHomePage();
          } else if (this.printingStatus === PrintStatus.pause) {
            this.dataService.changeInterval('printTime', false);
          } else if (this.printingStatus === PrintStatus.resume) {
            this.dataService.changeInterval('printTime', true);
            clearInterval(this.filamentInterval);
            this.checkFilamentFinish();
          }
        } else {
          console.log('problem in printPage->printAction!', data);
        }
      }, err => {
        console.error('error in changing state of printing', err);
      }
    );
  }

  openOptions() {
    this.dataService.lockAuthorize().then(res => {
      this.isOnOptionsPage = true;
    }, err => {})
  }

  closeOptions() {
    if (!this.isOnHeatPage && !this.isOnSpeedPage)
      this.isOnOptionsPage = false;
  }

  enableHeatPage() {
    if (this.isOnOptionsPage)
      this.isOnHeatPage = true;
  }

  disableHeatPage() {
    this.isOnHeatPage = false;
  }

  enableSpeedPage() {
    if (this.isOnOptionsPage)
      this.isOnSpeedPage = true;
  }

  disableSpeedPage() {
    this.isOnSpeedPage = false;
  }

  openFan() {
    if (!this.isOnOptionsPage)
      this.isOnFanPage = true;
  }

  changeFan(value) {
    this.progressService.enable();
    this.dataService.setFan(value)
      .then(res => {
        this.progressService.disable();
        this.isOnFanPage = false;
      })
      .catch(rej => {
        this.progressService.disable();
        console.warn('could not set the fan: ', rej);
      })
  }

  closeFan() {
    this.isOnFanPage = false;
  }

  // TODO: I THINK THIS IS NOW USELESS !!
  openDialog(kind = 'heat') {
    // TODO: heat is changed, so these should be changed to, instead of dialog, should be absoluted-page!
    // let kindComp = (kind === 'heat' ? HeatComponent : FanComponent);
    let kindComp = FanComponent;
    const rmDialog = this.dialog.open(kindComp, {
      width: (kind === 'heat' ? '640px' : '450px'),
      height: (kind === 'heat' ? '420px' : '200px'),
      data: {}
    });
    rmDialog.afterClosed().subscribe(
      data => {
        if (data && data['status'] === true) {
          this.progressService.enable();
          this.dataService.setHeat(data)
            .then(res => {
              this.progressService.disable();
              this.snackBar.open('دما با موفقیت تغییر کرد', null, {
                duration: 1700
              });
            })
            .catch(rej => {
              this.progressService.disable();
              console.error('heat problem in print page', rej);
            });
        }
      }
    );
  }

  getShortedString(text) {
    if (text.length > 29) {
      return text.substr(text.length - 27) + '...';
    } else {
      return text;
    }
  }

  goBackToHomePage() {
    this.router.navigate([`/home`]);
  }
}



