import {Component, OnInit, Input} from '@angular/core';
import {HttpService} from '../../services/http.service';
import {DataService} from '../../services/data.service';

@Component({
  selector: 'app-lock',
  templateUrl: './lock.component.html',
  styleUrls: ['./lock.component.css']
})
export class LockComponent implements OnInit {
  @Input() isOnSettings = false;

  code: string;
  onEnteringDialog = false;

  constructor(private httpService: HttpService, public dataService: DataService) {
  }

  ngOnInit() {
    this.resetData();
  }

  resetData() {
    this.clearCode();
    this.closeEnteringDialog();
  }

  lock() {
    this.httpService.post('lock', {}).subscribe(
      data => {
        console.log('data received from server for locking: ', data);
        this.dataService.isLocked = true;
      },
      err => {
        console.log('could not lock the device: ', err);
      }
    );
  }

  unlock() {
    if (this.code.length < 4)
      return;

    (this.isOnSettings ?
      this.httpService.put('set_pin', {code: this.code}) :
      this.httpService.post('unlock', {code: this.code})
    ).subscribe(
      data => {
        console.log('data from pin: ', data);
        this.dataService.isLocked = false;
        this.closeEnteringDialog();
        this.resetData();
      },
      err => {
        console.error('could not unlock/set the device: ', err);
        this.clearCode();
      }
    );
  }

  openEnteringDialog() {
    if (this.isOnSettings) {
      this.onEnteringDialog = true;
      this.clearCode();
    } else {
      if (this.dataService.isLocked) {
        this.onEnteringDialog = true;
        this.clearCode();
      } else {
        this.lock();
      }
    }
  }

  closeEnteringDialog() {
    this.onEnteringDialog = false;
  }

  clearCode() {
    this.code = '';
  }

  addNewCharacter(char) {
    if (this.code.length < 4)
      this.code = this.code + char;
  }

  removeLastCharacter() {
    this.code = this.code.substring(0, this.code.length - 1);
  }
}



