import { Injectable } from '@angular/core';
import {HttpClient} from "@angular/common/http";
import 'rxjs/add/operator/map';
import {Observable} from 'rxjs/Observable';

@Injectable()
export class HttpService {

  // public static Host = 'http://localhost:3000';
  // private serverAddress = 'http://172.18.129.94:8000/api/';
  private serverAddress = '/api/';
  defaultMap: Boolean = false;
  constructor(private http: HttpClient) {
  }

  get(url, map = this.defaultMap) : Observable<any> {
    if(!map)
      return this.http.get(this.serverAddress + url, {observe: 'response'});
    return this.http.get(this.serverAddress + url, {observe: 'response'}).map(data => data.body);
  }

  options(url, map = this.defaultMap) : Observable<any> {
    if(!map)
      return this.http.options(this.serverAddress + url, {observe: 'response'});
    return this.http.options(this.serverAddress + url, {observe: 'response'}).map(data => data.body);
  }

  put(url, values, map = this.defaultMap): Observable<any> {
    if(!map)
      return this.http.put(this.serverAddress + url, values, {observe: 'response'});
    return this.http.put(this.serverAddress + url, values, {observe: 'response'}).map(data => data.body);
  }

  post(url, values, map = this.defaultMap): Observable<any> {
    if(!map)
      return this.http.post(this.serverAddress + url, values, {observe: 'response'});
    return this.http.post(this.serverAddress + url, values, {observe: 'response'}).map(data => data.body);
  }

  delete(url, map = this.defaultMap): Observable<any> {
    if(!map)
      return this.http.delete(this.serverAddress + url, {observe: 'response'});
    return this.http.delete(this.serverAddress + url, {observe: 'response'}).map(data => data.body);
  }

}



