import {Component, OnInit} from '@angular/core';
import {ServerMatch} from "../../shared/servermatch";
import {Router} from "@angular/router";
import {HttpService} from "../../services/http.service";
import {MatSnackBar} from "@angular/material";

@Component({
  selector: 'app-homize',
  templateUrl: './homize.component.html',
  styleUrls: ['./homize.component.css']
})
export class HomizeComponent implements OnInit {

  elements: any;
  
  constructor(private router: Router, private httpService: HttpService,
              private snackBar: MatSnackBar) {
    let TIU = ServerMatch.STATIC + 'assets/images/print-logo.png';
    this.elements = {
      x: {
        title: 'X',
        imageUrl: TIU,
        axis: 'X',
      },
      y: {
        title: 'Y',
        imageUrl: TIU,
        axis: 'Y',
      },
      z: {
        title: 'Z',
        imageUrl: TIU,
        axis: 'Z',
      },
      all: {
        title: 'All',
        imageUrl: TIU,
        axis: 'All',
      },
      return: {
        title: 'بازگشت',
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
        routeTo: 'tools'
      }
    }
  }

  ngOnInit() {
  }

  goToPage(route) {
    this.router.navigate(['/' + route]);
  }

  homizeAxis(axis) {
    this.httpService.post('home', {axis}).subscribe(
      data => {
        this.snackBar.open('با موفقیت هوم شد!', null, {
          duration: 2300,
        });
      },
      err => {
        this.snackBar.open('ارور در ست کردن هوم', null, {
          duration: 2300,
        });
      }
    );
  }
}



