import {Component, OnInit} from '@angular/core';
import {ServerMatch} from '../../shared/servermatch';
import {Router} from '@angular/router';
import {HttpService} from '../../services/http.service';
import {ProgressService} from '../../services/progress.service';
import {MatDialog, MatSnackBar} from '@angular/material';
import {DataService} from '../../services/data.service';
import {ConfirmComponent} from '../../shared/confirm/confirm.component';
import {DialogUsage} from '../../shared/enum/dialogUsage';

@Component({
  selector: 'app-extrude',
  templateUrl: './extrude.component.html',
  styleUrls: ['../../base-template/base-template.component.css',
    './extrude.component.css']
})
export class ExtrudeComponent implements OnInit {

  elements: any;
  injected = 0;

  value: number = 2;
  feedrate: number = 500;

  constructor(private router: Router, private httpService: HttpService,
              private progressService: ProgressService, private snackBar: MatSnackBar,
              private dataService: DataService, private dialog: MatDialog) {
    this.elements = {
      ext_in: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png',
      },
      ext_out: {
        imageUrl: ServerMatch.STATIC + 'assets/images/down.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
        title: 'بازگشت',
      },
    }
  }

  ngOnInit() {
    if (this.dataService.temps.getValue().ext.cur < 178) {
      const rmDialog = this.dialog.open(ConfirmComponent, {
        width: '500px',
        data: {
          usage: DialogUsage.needsPreheating,
        },
      });
      rmDialog.afterClosed().subscribe(status => {
        this.goBackToToolsPage();
      });
    }
  }

  makeExtrude(isOut = false) {
    this.progressService.enable();
    this.httpService.post('extrude', {
      value: isOut ? this.value : -this.value,
      feedrate: this.feedrate
    }, true).subscribe(
      data => {
        this.progressService.disable();
        this.injected += isOut ? this.value : -this.value;
        this.snackBar.open('با موفقیت انجام شد', null, {
          duration: 1000
        });
      }, err => {
        this.progressService.disable();
        console.error('extrude problem:', err);
      }
    );
  }

  goBackToToolsPage() {
    this.router.navigate([`/tools`]);
  }
}



