import {Component, OnInit, HostListener} from '@angular/core';
import {ProgressService} from "./services/progress.service";
import {DataService} from './services/data.service';
import {HttpService} from './services/http.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  title = 'app';

  // by 'changes', I mean, the things that needs to be activated in that page!
  refreshingPages = [
    {
      pageName: 'main-page',
      changes: [
        'temps',
        'ips',
      ]
    },
    {
      pageName: 'print-page',
      changes: [
        'temps',
        'percentage',
        'printTime',
      ]
    },
    {
      pageName: 'tools-page',
      changes: [
        'temps',
      ]
    },
  ];

  standByTime = 0;
  isCurrentlyOn = true;

  // TODO: this needs testing with real server!
  @HostListener('document:click', ['$event'])
  clicked() {
    this.standByTime = 0;
    if (!this.isCurrentlyOn) {
      this.httpService.post('led', {status: 'on'}).subscribe(
        res => this.isCurrentlyOn = true,
        err => console.log('could not turn on led: ', err));
    }
  }

  constructor(private progressService: ProgressService, private dataService: DataService,
    private httpService: HttpService) {

    setInterval(() => {
      this.standByTime += 10;
      if (this.standByTime > this.dataService.standByLimit) {
        this.httpService.post('led', {status: 'off'}).subscribe(
          res => this.isCurrentlyOn = false,
          err => console.log('could not turn off led: ', err));
      }
    }, 10000);
  }

  showProgressing = false;
  color: any = 'primary';
  mode: any;
  value: any;
  bufferValue: any;

  ngOnInit() {
    this.settingProgressService();
  }

  settingProgressService() {
    this.progressService.showProgress.subscribe(
      (data) => this.showProgressing = data,
      (err) => {
        this.showProgressing = false;
        console.error('An error occurred when subscribing on showProgress in progressService: ', err);
      }
    );

    this.progressService.progressMode.subscribe(
      (data) => this.mode = data,
      (err) => {
        this.mode = null;
        console.error('An error occurred when subscribing on progressMode in progressService: ', err);
      }
    );

    this.progressService.progressValue.subscribe(
      (data) => this.value = data,
      (err) => {
        this.value = null;
        console.error('An error occurred when subscribing on progressValue in progressService: ', err);
      }
    );

    this.progressService.progressBufferValue.subscribe(
      (data) => this.bufferValue = data,
      (err) => {
        this.bufferValue = null;
        console.error('An error occurred when subscribing on progressBufferValue in progressService: ', err);
      }
    );
  }

  refreshData($event, activate) {
    this.dataService.datas.forEach(e => {
      this.dataService.changeInterval(e, false);
    });
    this.progressService.disable();
    this.refreshingPages.forEach(e => {
      if ($event.hasOwnProperty('pageName') && $event['pageName'] == e['pageName']) {
        e.changes.forEach(change => {
          this.dataService.changeInterval(change, true);
        })
      }
    });
  }
}



