import {Component, OnInit} from '@angular/core';
import {HttpService} from "../../services/http.service";
import {Router} from "@angular/router";
import {ProgressService} from "../../services/progress.service";
import {ServerMatch} from "../../shared/servermatch";
import {MatDialog} from '@angular/material';
import {ConfirmComponent} from '../../shared/confirm/confirm.component';
import {DialogUsage} from '../../shared/enum/dialogUsage';

interface Axis {
  X: Number;
  Y: Number;
  Z: Number;
}

@Component({
  selector: 'app-move-axis',
  templateUrl: './move-axis.component.html',
  styleUrls: ['./move-axis.component.css']
})
export class MoveAxisComponent implements OnInit {

  haveAccess = false;

  elements: any;
  value: number = 20;
  axis: Axis;

  constructor(private httpService: HttpService, private router: Router,
              private progressService: ProgressService, private dialog: MatDialog) {
    this.elements = {
      up: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png'
      },
      down: {
        imageUrl: ServerMatch.STATIC + 'assets/images/down.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      }
    }
  }

  ngOnInit() {
    // check for accessing to this page!
    this.haveAccess = false;
    this.progressService.enable();
    this.httpService.options('move_axis', true).subscribe(
      data => {
        this.progressService.disable();
        if (data && data['access'] === true) {
          this.haveAccess = true;
        } else {
          this.notHomed();
        }
      }, err => {
        this.progressService.disable();
        console.error('could not gain access for moving!', err);
        this.notHomed();
      }
    );

    this.axis = {
      X: 0,
      Y: 0,
      Z: 0,
    };
  }

  notHomed() {
    const rmDialog = this.dialog.open(ConfirmComponent, {
      width: '500px',
      data: {
        usage: DialogUsage.needsHoming,
      },
    });
    rmDialog.afterClosed().subscribe(status => {
      this.goToToolsPage();
    })
  }

  moveAxis(axis, value) {
    if (!this.haveAccess) {
      console.error('you have no access to use this page, period!');
      return;
    }
    // if(this.axis[axis] + value < 0) {
    //   console.log("YOU'RE SETTING IT TO MINUS!");
    //   return;
    // }

    this.progressService.enable();
    this.httpService.post('move_axis', {axis, value}).subscribe(
      data => {
        this.progressService.disable();
        this.axis[axis] += value || 0;
      },
      err => {
        this.progressService.disable();
        console.error("couldn't move axis", err);
      }
    );
  }

  goToToolsPage() {
    this.router.navigate(['/tools']);
  }

  precisionRound(number, precision) {
    var factor = Math.pow(10, precision);
    return Math.round(number * factor) / factor;
  }

}



