import {Component, OnInit, Output, EventEmitter} from '@angular/core';
import {DataService} from '../../services/data.service';
import {HttpService} from '../../services/http.service';
import {ProgressService} from '../../services/progress.service';
import {MatSnackBar} from '@angular/material';
import {ServerMatch} from '../servermatch';

@Component({
  selector: 'app-print-speed',
  templateUrl: './print-speed.component.html',
  styleUrls: [
    '../../base-template/base-template.component.css',
    '../heat/heat.component.css',
    './print-speed.component.css'
  ]
})
export class PrintSpeedComponent implements OnInit {

  @Output() onClose = new EventEmitter();

  elements: any;

  result: {
    kind: string,
    value: number,
  } = {
      kind: 'travel',
      value: 100
    };

  travelValue = 100;
  feedrateValue = 100;

  travelUnit = 1;
  feedrateUnit = 1;

  constructor(private httpService: HttpService, private dataService: DataService,
    private progressService: ProgressService, private snackBar: MatSnackBar) {
    this.elements = {
      left: {
        imageUrl: ServerMatch.STATIC + 'assets/images/left.png',
      },
      right: {
        imageUrl: ServerMatch.STATIC + 'assets/images/right.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
    };
    this.getValuesFromService();
  }

  getValuesFromService() {
    this.travelValue = this.dataService.speed['travel'];
    this.feedrateValue = this.dataService.speed['feedrate'];
  }

  ngOnInit() {
    this.getValuesFromService();
  }

  changeValue(kind, relNumber = 0, absNumber = 0) { // kind = 'travel' || 'feedrate'
    if (kind === 'travel') {
      const goal = relNumber ? this.travelValue + relNumber : (absNumber ? absNumber : 0);
      this.changesTakeAction(kind, goal);
    } else if (kind === 'feedrate') {
      const goal = relNumber ? this.feedrateValue + relNumber : (absNumber ? absNumber : 0);
      this.changesTakeAction(kind, goal);
    }
  }

  onTravelChanged() {
    if (this.travelUnit === 1)
      this.travelUnit = 5;
    else if (this.travelUnit === 5)
      this.travelUnit = 10;
    else
      this.travelUnit = 1;
  }

  onFeedrateChanged() {
    if (this.feedrateUnit === 1)
      this.feedrateUnit = 5;
    else if (this.feedrateUnit === 5)
      this.feedrateUnit = 10;
    else
      this.feedrateUnit = 1;
  }

  close() {
    this.onClose.emit();
  }

  changesTakeAction(kind, value) {
    this.progressService.enable();
    this.dataService.setSpeed(kind, value)
      .then(res => {
        this.progressService.disable();
        this.getValuesFromService();
      })
      .catch(rej => {
        this.progressService.disable();
        console.error('speed problem in print page', rej);
      });
  }
}



