import {BrowserModule} from '@angular/platform-browser';
import {NgModule} from '@angular/core';


import {AppComponent} from './app.component';
import {MainPageComponent} from './main-page/main-page.component';
import {SelectUsbComponent} from './select-usb/select-usb.component';
import {routing} from "./app.routing";
import {FlexLayoutModule} from "@angular/flex-layout";
import {
  MatButtonModule,
  MatCardModule, MatCheckboxModule,
  MatDialogModule, MatFormFieldModule,
  MatIconModule,
  MatInputModule, MatOptionModule,
  MatProgressBarModule,
  MatProgressSpinnerModule, MatSelectModule,
  MatSliderModule,
  MatSnackBarModule, MatTabsModule, MatTableModule,
} from "@angular/material";
import {MatKeyboardModule} from '@ngx-material-keyboard/core';
import {HttpService} from "./services/http.service";
import {HttpClientModule} from "@angular/common/http";
import {ProgressService} from "./services/progress.service";
import {BrowserAnimationsModule} from "@angular/platform-browser/animations";
import {ToolsPageComponent} from './tools-page/tools-page.component';
import {HomizeComponent} from './tools-page/homize/homize.component';
import {MoveAxisComponent} from './tools-page/move-axis/move-axis.component';
import {PrintPageComponent} from './print-page/print-page.component';
import {DataService} from './services/data.service';
import {ConfirmComponent} from './shared/confirm/confirm.component';
import {SettingsPageComponent} from './settings-page/settings-page.component';
import {WifiComponent} from './settings-page/wifi/wifi.component';
import {FormsModule} from '@angular/forms';
import {ExtrudeComponent} from './tools-page/extrude/extrude.component';
import {BedlevelingComponent} from './tools-page/bedleveling/bedleveling.component';
import {BaseTemplateComponent} from './base-template/base-template.component';
import {FanComponent} from './shared/fan/fan.component';
import {HeatComponent} from './shared/heat/heat.component';
import {LockComponent} from './shared/lock/lock.component';
import {AngularFontAwesomeModule} from 'angular-font-awesome';
import {PrintSpeedComponent} from './shared/print-speed/print-speed.component';
import {LastPrintsComponent} from './settings-page/last-prints/last-prints.component';
import {CommonModule} from '../../node_modules/@angular/common';
import {VariableService} from './services/variable.service';

@NgModule({
  declarations: [
    AppComponent,
    MainPageComponent,
    SelectUsbComponent,
    ToolsPageComponent,
    HomizeComponent,
    MoveAxisComponent,
    PrintPageComponent,
    ConfirmComponent,
    SettingsPageComponent,
    WifiComponent,
    ExtrudeComponent,
    BedlevelingComponent,
    BaseTemplateComponent,
    FanComponent,
    HeatComponent,
    LockComponent,
    PrintSpeedComponent,
    LastPrintsComponent,
  ],
  imports: [
    routing,
    CommonModule,
    BrowserAnimationsModule,
    BrowserModule,
    HttpClientModule,
    FormsModule,
    FlexLayoutModule,
    AngularFontAwesomeModule,
    MatSnackBarModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatIconModule,
    MatFormFieldModule,
    MatInputModule,
    MatSelectModule,
    MatOptionModule,
    MatSliderModule,
    MatCardModule,
    MatDialogModule,
    MatButtonModule,
    MatKeyboardModule,
    MatCheckboxModule,
    MatTabsModule,
    MatTableModule,
  ],
  entryComponents: [
    ConfirmComponent,
    // HeatComponent,
    FanComponent,
  ],
  providers: [HttpService, ProgressService, DataService, VariableService],
  bootstrap: [AppComponent],
})
export class AppModule {
}



