import {Component, Inject, OnInit} from '@angular/core';
import {MAT_DIALOG_DATA, MatDialogRef} from '@angular/material';
import {DialogUsage} from '../enum/dialogUsage';
import {VariableService} from '../../services/variable.service';

@Component({
  selector: 'app-confirm',
  templateUrl: './confirm.component.html',
  styleUrls: ['./confirm.component.css']
})
export class ConfirmComponent implements OnInit {

  usage;
  printInfo;

  constructor(public dialogRef: MatDialogRef<ConfirmComponent>, @Inject(MAT_DIALOG_DATA) public data: any,
              public variableService: VariableService) {
  }

  ngOnInit() {
    this.usage = this.data && this.data['usage'];

    if (this.usage === DialogUsage.FinishPrinting) {
      this.printInfo = {
        dir: this.variableService.gcodePrintingFileDirectory,
        time: this.variableService.stringizedTime(this.variableService.currentPrintTime),
      }
    }
    else if (this.usage === DialogUsage.shouldGoToPrintPage) {
      this.autoRemove();
    }
    else if (this.usage === DialogUsage.UnfinishedPrinting) {

    }
    // data = (this.data && this.data.name) ? this.data.name : null;
  }

  autoRemove() {
    setTimeout(() => {
      this.dialogRef.close(true);
    }, 3500)
  }

  remove(answer) {
    this.dialogRef.close(answer);
  }

}



