import {Component, OnInit, ViewChild} from '@angular/core';
import {DataService} from '../../services/data.service';
import {HttpService} from '../../services/http.service';
import {MatSnackBar} from '@angular/material';
import {ServerMatch} from '../../shared/servermatch';
import {Router} from '@angular/router';
import {ProgressService} from '../../services/progress.service';

@Component({
  selector: 'app-wifi',
  templateUrl: './wifi.component.html',
  styleUrls: ['./wifi.component.css']
})
export class WifiComponent implements OnInit {

  elements: any;

  wifis = ['rahim', 'karim', 'reza'];
  selectedWifi = '';
  password = '';
  isPassVisible = false;

  constructor(public dataService: DataService, private httpService: HttpService,
              private snackBar: MatSnackBar, private router: Router,
              private progressService: ProgressService) {
    this.elements = {
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      }
    };
  }

  ngOnInit() {
    this.progressService.enable();
    this.httpService.options(`wifi`, true).subscribe(
      data => {
        this.progressService.disable();
        this.wifis = data['list'];
      }, err => {
        this.progressService.disable();
        this.snackBar.open("Cannot get list of wifis", null, {
          duration: 2000
        });
      }
    )
  }

  submitWifi() {
    // console.log(this.selectedWifi, this.password);
    this.progressService.enable();
    this.httpService.post(`wifi`, {
      ssid: this.selectedWifi,
      password: this.password,
    }, true).subscribe(
      data => {
        if (data && data['status'] === 'success') {
          this.dataService.connectedWifi = this.selectedWifi;
          this.password = '';
          // console.log('got the wifi! now going for the setting the ips!');
          this.dataService.setIpList()
            .then(res => {
            this.progressService.disable();
            this.snackBar.open('دستگاه با موفقیت به وای فای متصل شد.', null, {
              duration: 2000,
            });
          }).catch(rej => {
            this.progressService.disable();
            this.snackBar.open('خطای اتصال به وای فای', null, {
              duration: 2000
            });
          })
        }
        else if (data && data['status'] === 'failure') {
          this.progressService.disable();
          console.error('can not connect to wifi! error with failure!');
        }
        else {
          this.progressService.disable();
          console.error('neither ways!');
        }
      }, err => {
        this.progressService.disable();
        console.error('connection problem!', err);
      }
    );
  }

  goBackToSettingsPage() {
    this.router.navigate([`/settings`]);
  }

}



