import {Component, Inject, OnInit, Output, EventEmitter} from '@angular/core';
import {HttpService} from '../../services/http.service';
import {MAT_DIALOG_DATA, MatDialogRef, MatSnackBar} from '@angular/material';
import {ServerMatch} from '../servermatch';
import {DataService} from '../../services/data.service';
import {ProgressService} from '../../services/progress.service';

@Component({
  selector: 'app-heat',
  templateUrl: './heat.component.html',
  styleUrls: ['../../base-template/base-template.component.css',
    './heat.component.css']
})
export class HeatComponent implements OnInit {

  @Output() onClose = new EventEmitter();

  elements: any;

  result: {
    status: Boolean, // true for submission, false for declined
    kind: String, // 'bed' || 'extruder'
    goal: number, // the value that we want to set the goal to
  };

  bed_goal: number;
  ext_goal: number;

  extSliderValueShown = 1;
  bedSliderValueShown = 1;

  constructor(private httpService: HttpService, private dataService: DataService,
    private progressService: ProgressService, private snackBar: MatSnackBar) {
    this.elements = {
      cooldown: {
        imageUrl: ServerMatch.STATIC + 'assets/images/cooldown.png',
      },
      left: {
        imageUrl: ServerMatch.STATIC + 'assets/images/left.png',
      },
      right: {
        imageUrl: ServerMatch.STATIC + 'assets/images/right.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
    };
    this.result = {
      status: true,
      kind: 'bed', // || 'hotend'
      goal: this.dataService.temps.getValue()['ext'].goal || 0,
    };
    this.bed_goal = this.dataService.temps.getValue()['bed'].goal || 0;
    this.ext_goal = this.dataService.temps.getValue()['ext'].goal || 0;
  }

  ngOnInit() {
    this.bed_goal = this.dataService.temps.getValue()['bed'].goal || 0;
    this.ext_goal = this.dataService.temps.getValue()['ext'].goal || 0;
  }

  changeValue(absNumber, isNegative = false, kind = 'bed', absolute = true) { //kind: 'bed' || 'hotend'
    if (absolute) {
      if (kind === 'bed')
        this.bed_goal = absNumber;
      else
        this.ext_goal = absNumber;
    }
    else {
      if (kind === 'bed')
        this.bed_goal += (absNumber !== 0 ? absNumber : (isNegative ? -this.bedSliderValueShown : this.bedSliderValueShown));
      else
        this.ext_goal += (absNumber !== 0 ? absNumber : (isNegative ? -this.extSliderValueShown : this.extSliderValueShown));
    }
  }

  onExtSliderValueChanged() {
    if (this.extSliderValueShown === 1)
      this.extSliderValueShown = 5;
    else if (this.extSliderValueShown === 5)
      this.extSliderValueShown = 10;
    else
      this.extSliderValueShown = 1;
  }

  onBedSliderValueChanged() {
    if (this.bedSliderValueShown === 1)
      this.bedSliderValueShown = 5;
    else if (this.bedSliderValueShown === 5)
      this.bedSliderValueShown = 10;
    else
      this.bedSliderValueShown = 1;
  }

  remove(status, kind = 'bed', goal = -1) {
    if (!status) { //return
      this.onClose.emit();
      return;
    }

    this.result['status'] = status;
    this.result['kind'] = kind;
    this.result['goal'] = (kind === 'bed' ? this.bed_goal : this.ext_goal);

    if (goal === 0)
      this.result['goal'] = goal;

    this.changesTakeAction(this.result);
  }

  changesTakeAction(result) {
    console.log('the result: ', result);

    if (result && result['status'] === true) {
      this.progressService.enable();
      this.dataService.setHeat(result)
        .then(res => {
          this.progressService.disable();
          this.snackBar.open('دما با موفقیت تغییر کرد', null, {
            duration: 1800
          });
        })
        .catch(rej => {
          this.progressService.disable();
          console.error('heat problem in print page', rej);
        });
    }
  }
}



