import {Injectable} from '@angular/core';
import {BehaviorSubject} from 'rxjs/BehaviorSubject';
import {ProgressService} from './progress.service';
import {HttpService} from './http.service';
import {Router} from '@angular/router';
import {MatDialog} from '@angular/material';
import {DialogUsage} from '../shared/enum/dialogUsage';
import {ConfirmComponent} from '../shared/confirm/confirm.component';
import {VariableService} from './variable.service';

export interface Temperature {
  bed: {
    cur: number,
    goal: number,
  };
  ext: {
    cur: number,
    goal: number
  };
}

@Injectable()
export class DataService {
  datas = [
    'temps',
    'percentage',
    'ips',
  ];

  firstTouch = true;

  temps: BehaviorSubject<Temperature> = new BehaviorSubject<Temperature>({
    bed: {
      cur: 0,
      goal: 10,
    },
    ext: {
      cur: 0,
      goal: 10,
    },
  });
  tempsInterval;

  speed: {
    travel: number,
    feedrate: number,
  } = {
      travel: 100,
      feedrate: 100,
    };

  // currentPercentage = 0;
  zPosition: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  percentage: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  percentageInterval;

  printTime: BehaviorSubject<number> = new BehaviorSubject<number>(0);
  printTimeInterval;

  connectedWifi = '';
  ipList = [];
  ipInterval;

  onPrintPageInterval;

  isLocked = false;
  askBefore = true;

  standByLimit = 23; //5 * 60; // TODO: seconds

  constructor(private progressService: ProgressService, private httpService: HttpService,
    private router: Router, private dialog: MatDialog, private variableService: VariableService) {
    this.setTemps();

    // checks for whether should be going/leaving to print page
    this.checkOnPrintPage();
  }

  /**
   * gets the initial value of AskBefore
   * this is useful for when we have an unfinished project
   * and we want to ask the user to continue or not (false)
   * or defaultly continue without asking (true)
   */
  getAskBefore() {
    return new Promise((resolve, reject) => {
      this.httpService.get('abs', true).subscribe(
        data => {
          this.askBefore = data['abs'] || true;
          resolve(data);
        }, err => {
          console.warn('error getting ask before', err);
          reject(err);
        }
      );
    });
  }

  /**
   * the simple authorization that the child lock provides,
   * in order to let/prohibit the user from having accesses
   */
  lockAuthorize() {
    return new Promise((resolve, reject) => {
      if (!this.isLocked) {
        resolve();
      } else {
        reject();
      }
    });
  }

  setUpBasic(name) {
    if (name === 'percentage') {
      this.percentage.next(0);
      // TODO: maybe we don't need this anymore!
    } else if (name === 'printTime') {
      this.variableService.currentPrintTime = 0;
      this.printTime.next(0);
    }
  }

  /**
   * this checks for unfinished project and if there was, we show the dialog of
   * 'redirecting ...' and also send a request to the server to continue that
   */
  checkUnfinishedProject() {
    return new Promise((resolve, reject) => {
      this.checkOnPrintPageFunctionInside().then(res => {
        if (res === true) {
          return reject(false);
        }

        this.httpService.post('print', {action: 'unfinished'}, true).subscribe(
          data => {
            if (data['status'] === 'success') {
              if (data['unfinished'] && data['unfinished']['exist']) {
                console.log('received data for unfinished file:', data);
                this.variableService.gcodePrintingFileDirectory = data['unfinished']['cd'];
                this.variableService.gcodeUnfinishedLine = data['unfinished']['line'];
                resolve(true);
              }
            }
          }, err => {
            reject(false);
          }
        )
      })
    });
  }

  /**
   * checks every 10 seconds to see if there is something printing.
   * useful when we are connected with two devices and we set one
   * to print, so the other will know there's a print going on and
   * redirects to the printing page OR we stop a print on one device
   * and the other connected devices should be aware and go to home
  */
  checkOnPrintPage() {
    this.onPrintPageInterval = setInterval(() => {
      this.checkOnPrintPageFunctionInside();
    }, 10000);
  }

  /**
   * checks if there is a printing going on
   */
  checkOnPrintPageFunctionInside() {
    return new Promise((resolve, reject) => {
      this.httpService.post('on_print_page', {}, true)
        .subscribe(
          data => {
            if (data && data['page'] === 'print') {
              // should go to the print page if not already in it
              this.openDialogForGoingToPrintPage();
              resolve(true);
            } else {
              //should go to home page if it's on the print page
              this.redirectBackUserToHomePage();
              resolve(false);
            }
          }, err => {
            resolve(false);
          }
        );
    });
  }

  /**
   * redirects to printing page (due to an ongoing printing)
   */
  openDialogForGoingToPrintPage() {
    if (this.router.url === '/print')
      return;

    const rmDialog = this.dialog.open(ConfirmComponent, {
      width: '450px',
      data: {
        usage: DialogUsage.shouldGoToPrintPage,
      }
    });
    rmDialog.afterClosed().subscribe(status => {
      this.router.navigate([`/print`]);
    });
  }

  /**
   * redirects BACK from printing page (due to print problem, stopping, etc.)
   */
  redirectBackUserToHomePage() {
    if (this.router.url !== '/print')
      return;

    this.router.navigate(['/home']);
  }

  /**
   * @param name 'temps' | 'percentage' | 'printTime' | 'ips'
   * @param activate 
   * depending on which page we're currently in,
   * make the necessary changes in the intervals
   */
  changeInterval(name, activate = true) {
    if (!activate) {
      if (name === 'temps') {
        clearInterval(this.tempsInterval);
      } else if (name === 'percentage') {
        clearInterval(this.percentageInterval);
      } else if (name === 'printTime') {
        clearInterval(this.printTimeInterval);
      } else if (name === 'ips') {
        clearInterval(this.ipInterval);
      }
      // this.changeInterval(this[name]) // Don't know what it was doing exactly :|
    } else {
      if (name === 'temps') {
        this.setTemps();
      } else if (name === 'percentage') {
        this.setPercentageAndZ();
      } else if (name === 'printTime') {
        this.setPrintTime();
      } else if (name === 'ips') {
        this.setIpInterval();
      }
    }
  }


  setTemps() {
    this.tempsInterval = setInterval(() => {
      if (this.firstTouch)
        this.progressService.enable();
      this.httpService.get('temperatures', true).subscribe(
        (data) => {
          this.temps.next({
            bed: data.bed,
            ext: data.ext
          });
          if (this.firstTouch) {
            this.progressService.disable();
            this.firstTouch = false;
          }
        }, (err) => {
          if (!this.firstTouch) {
            this.progressService.disable();
            this.firstTouch = true;
            console.log("server didn't send temperatures", err);
          }
        }
      );
    }, 2000);
  }

  setHeat(data) {
    return new Promise((resolve, reject) => {
      const sendingObj = {
        field: data['kind'] === 'bed' ? 'bed' : 'hotend',
        action: data['goal'] === 0 ? 'cooldown' : 'heat',
        value: data['goal']
      };

      this.httpService.post('heat', sendingObj).subscribe(
        data => {
          if (data['status'] === 'success') {
            resolve(true);
          }
          else {
            reject(false);
          }
        }, err => {
          reject(false);
        }
      );
    })
  }

  setSpeed(kind, value) {
    return new Promise((resolve, reject) => {
      const sendingObj = {
        field: kind, // 'travel' || 'feedrate'
        value: value // the goal speed value
      };

      this.httpService.post('speed', sendingObj, false).subscribe(
        data => {
          this.speed[sendingObj.field] = sendingObj.value;
          resolve(true);
        },
        err => {
          reject();
        }
      );
    });
  }

  setFan(status) {
    return new Promise((resolve, reject) => {
      this.httpService.post('fan_speed', {status}, true).subscribe(
        data => {
          resolve(true);
        },
        err => {
          reject();
        }
      )
    });
  }

  setIpList() {
    return new Promise((resolve, reject) => {
      this.httpService.post('ip', {}, true).subscribe(
        data => {
          // console.log('data received from ip:', data);
          if (data && data['ips']) {
            this.ipList = data['ips'];
            resolve(true);
          }
          else {
            console.error('in else if posting to ip!', data);
            reject(false);
          }
        }, err => {
          console.error('in err of posting to ip', err);
          reject(false);
        }
      );
    })
  }

  /**
   * the 5" interval for getting % of printing and current Z position
   */
  setPercentageAndZ() {
    this.percentageInterval = setInterval(() => {
      this.httpService.post(`print`, {
        action: 'percentage'
      }, true).subscribe(
        data => {
          this.percentage.next(data['percentage']);
        }, err => {
          console.warn('percentage fetch error', err);
        }
      );
      this.httpService.get('get_z', true).subscribe(
        data => {
          this.zPosition.next(data['z']);
        }, err => {
          console.warn('could not get z', err);
        }
      );
    }, 5000);
  }

  /**
   * the 1' interval of getting the printing time from the server
   */
  setPrintTime() {
    this.printTimeInterval = setInterval(() => {
      this.httpService.get('get_time', true).subscribe(
        data => {
          this.variableService.currentPrintTime = data['time'];
          this.printTime.next(this.variableService.currentPrintTime);
        }, err => {
          console.warn('print time couldn\'t be fetched', err);
        }
      );
    }, 1 * 60 * 1000); // 1 min
  }

  /**
   * the 15" interval for getting the ip list
   */
  setIpInterval() {
    this.setIpList();
    this.ipInterval = setInterval(() => {
      this.setIpList();
    }, 15000);
  }
}



