import {Component, OnInit} from '@angular/core';
import {ServerMatch} from '../shared/servermatch';

@Component({
  selector: 'app-base-template',
  templateUrl: './base-template.component.html',
  styleUrls: ['./base-template.component.css']
})
export class BaseTemplateComponent implements OnInit {

  elements: any;

  constructor() {
    this.elements = {
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
      ext_in: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png',
      },
      ext_out: {
        imageUrl: ServerMatch.STATIC + 'assets/images/down.png',
      },
    }
  }

  ngOnInit() {
  }

}



