import {Component, OnInit} from '@angular/core';
import {HttpService} from "../services/http.service";
import {MatSnackBar} from "@angular/material";
import {ProgressService} from "../services/progress.service";
import {ServerMatch} from "../shared/servermatch";
import {Router} from "@angular/router";
import {DataService} from '../services/data.service';
import {VariableService} from '../services/variable.service';

@Component({
  selector: 'select-usb-page',
  templateUrl: './select-usb.component.html',
  styleUrls: ['./select-usb.component.css']
})
export class SelectUsbComponent implements OnInit {

  // initial temp data
  // usbs = ['temp1 temp1temp1', 'temp2.gcode', 'temp3 temp3.gcode', 'temp4temp4temp4.gcode', 'temp5temp5 .gcode', 'temp6temp6 temp6'];
  usbs = [];

  cd = '';

  i = 0;

  elements: any;

  constructor(private httpService: HttpService, private dataService: DataService,
    private progressService: ProgressService, private router: Router,
    private variableService: VariableService) {
    this.elements = {
      left: {
        imageUrl: ServerMatch.STATIC + 'assets/images/left.png',
      },
      right: {
        imageUrl: ServerMatch.STATIC + 'assets/images/right.png',
      },
      up: {
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
      usb: {
        imageUrl: ServerMatch.STATIC + 'assets/images/usb.png',
      },
      folder: {
        imageUrl: ServerMatch.STATIC + 'assets/images/folder.png',
      },
      gcodeFile: {
        imageUrl: ServerMatch.STATIC + 'assets/images/file.png',
      },
      none: {
        imageUrl: ServerMatch.STATIC + 'assets/images/none.png',
      },
    }
  }

  ngOnInit() {
    setTimeout(() => {
      this.progressService.enable();
      console.log('post to dir:', this.cd);
      this.httpService.post('directory', {'cd': this.cd}, true).subscribe(
        data => {
          this.progressService.disable();
          if (data.data.length === 0 || (data.data.length === 1 && data.data[0] === '')) {
            this.usbs = [];
          }
          else {
            this.usbs = data.data;
          }
        }, err => {
          this.progressService.disable();
          console.log("server didn't send usbs", err);
        }
      );
    }, 0)
  }

  addDir(path, isAdd = true) {
    let currentDir = this.cd;
    if (path && path != '') {
      if (isAdd) {
        if (currentDir != '') {
          currentDir += '/' + path;
        } else {
          currentDir = path;
        }
      } else {
        const tempDir = path.split('/');
        currentDir = tempDir.splice(0, tempDir.length - 1).join('/');
      }

      console.log('post to dir:', currentDir);
      this.progressService.enable();
      this.httpService.post('directory', {'cd': currentDir}, true).subscribe(
        data => {
          this.cd = currentDir;
          console.log('data received:', data);

          if (data && data.type == 'file') { // necessarily .gcode
            this.makeThePrint();
          } else {
            this.progressService.disable();
            this.usbs = data.data;
            this.i = 0;
          }
        }, err => {
          this.progressService.disable();
          console.log('server didn\'t send the files', err);
        }
      );
    }
  }

  makeThePrint() {
    this.variableService.gcodePrintingFileDirectory = this.cd;
    this.httpService.post('print', {'cd': this.cd, 'action': 'print'}, true).subscribe(
      data => {
        if (data.status === 'success') {
          this.progressService.disable();
          this.router.navigate([`/print`]);
        } else {
          console.error('problem occurred!', data);
        }
      }, err => {
        this.progressService.disable();
        console.error('go to print page has problems: ', err);
      }
    );
  }

  changePage(next) {
    if (next) {
      if (this.i < this.usbs.length - 4) {
        this.i += 4;
        console.log('added', this.i);
      }
    } else {
      if (this.i >= 4) {
        this.i -= 4;
        console.log('removed', this.i);
      }
    }
  }

  getShortString(text: string, length = 13) {
    const gcode = '.gcode';
    const dots = '...';

    if (this.isGcode(text)) {
      text = text.substring(0, text.length - gcode.length);
    }

    if (text.length < length) {
      return text;
    } else {
      return text.substring(0, length - dots.length) + dots;
    }

    // default
    // return text.substring(0, length);
  }

  isGcode(text: string) {
    const gcode = '.gcode';
    return text.endsWith(gcode);
  }

  goBackToHomePage() {
    this.router.navigate([`/home`]);
  }

}



