import { Component, OnInit } from '@angular/core';
import {HttpService} from '../../services/http.service';
import {ProgressService} from '../../services/progress.service';
import {Router} from '@angular/router';
import {MatSnackBar} from '@angular/material';
import {ServerMatch} from '../../shared/servermatch';

@Component({
  selector: 'app-bedleveling',
  templateUrl: './bedleveling.component.html',
  styleUrls: ['./bedleveling.component.css']
})
export class BedlevelingComponent implements OnInit {

  elements: any;

  constructor(private httpService: HttpService, private progressService: ProgressService,
              private router: Router, private snackBar: MatSnackBar) {
    this.elements = {
      stage: {
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png',
      },
      return: {
        title: 'بازگشت',
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      }
    }
  }

  ngOnInit() {
  }

  levelStage(stage) {
    this.progressService.enable();
    this.httpService.post('bedleveling', {stage}, true).subscribe(
      data => {
        this.progressService.disable();
        this.snackBar.open('Successfully leveled!', null, {
          duration: 1500,
        });
      }, err => {
        this.progressService.disable();
        console.error('connection refused!', err);
      }
    )
  }

  goBackToToolsPage() {
    this.router.navigate([`/tools`]);
  }

}



