import {Component, OnInit} from '@angular/core';
import {ServerMatch} from "../shared/servermatch";
import {Router} from "@angular/router";
import {HttpService} from '../services/http.service';
import {MatDialog, MatSnackBar} from '@angular/material';
import {ProgressService} from '../services/progress.service';
import {HeatComponent} from '../shared/heat/heat.component';
import {FanComponent} from '../shared/fan/fan.component';
import {DataService} from '../services/data.service';

@Component({
  selector: 'app-tools-page',
  templateUrl: './tools-page.component.html',
  styleUrls: ['./tools-page.component.css']
})
export class ToolsPageComponent implements OnInit {
  pageName = 'tools-page';

  bed = {
    cur: 0,
    goal: 10,
  };

  ext = {
    cur: 0,
    goal: 10,
  };

  elements: any;

  isOnHeatPage = false;

  constructor(private router: Router, private httpService: HttpService,
              private snackBar: MatSnackBar, private progressService: ProgressService,
              private dialog: MatDialog, public dataService: DataService) {
    let TIU = ServerMatch.STATIC + 'assets/images/print-logo.png';
    this.elements = {
      home: {
        title: 'Home',
        imageUrl: TIU,
        routeTo: 'homize',
      },
      heat: {
        title: 'Heat',
        imageUrl: TIU,
        routeTo: 'heat',
      },
      move: {
        title: 'Move',
        imageUrl: TIU,
        routeTo: 'move',
      },
      ext: {
        title: 'Extrude',
        imageUrl: TIU,
        routeTo: 'extrude',
      },
      return: {
        title: 'بازگشت',
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
        routeTo: 'home',
      },
      release_motor: {
        title: 'آزادکردن موتور',
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png',
      },
      bed: {
        title: 'Bed Leveling',
        imageUrl: ServerMatch.STATIC + 'assets/images/bed-leveling.png',
        routeTo: 'bedleveling',
      },
      fan: {
        title: 'Fans',
        imageUrl: ServerMatch.STATIC + 'assets/images/fans.png',
        routeTo: 'setfans',
      }
    };
  }

  ngOnInit() {
    this.dataService.temps.subscribe(data => {
      this.bed = data.bed;
      this.ext = data.ext;
    });
  }

  goToPage(route) {
    this.router.navigate(['/' + route]);
  }

  enableHeatPage() {
    this.isOnHeatPage = true;
  }

  disableHeatPage() {
    this.isOnHeatPage = false;
  }

  openDialog(kind = 'heat') {
    // TODO: this should not be opened in a popup, but an absoluted-size page
    // let kindComp = (kind === 'heat' ? HeatComponent : FanComponent);
    let kindComp = FanComponent;
    const rmDialog = this.dialog.open(kindComp, {
      width: (kind === 'heat' ? '640px' : '450px'),
      height: (kind === 'heat' ? '420px' : '200px'),
      data: {}
    });
    rmDialog.afterClosed().subscribe(
      data => {
        if (data && data['status'] === true) {
          this.progressService.enable();
          this.dataService.setHeat(data)
            .then(res => {
              this.progressService.disable();
              this.snackBar.open('دما با موفقیت تغییر کرد', null, {
                duration: 1700
              });
            })
            .catch(rej => {
              this.progressService.disable();
              console.error('heat problem!', rej);
            })
        }
      }
    );
  }

  releaseMotor() {
    this.progressService.enable();
    this.httpService.post('release_motor', true).subscribe(
      data => {
        this.progressService.disable();
        this.snackBar.open('موتورها با موفقیت آزاد شدند', null, {
          duration: 2000
        });
      }, err => {
        this.progressService.disable();
        console.error('connection refused!', err);
      }
    );
  }

}



