import {Component, OnInit} from '@angular/core';
import {ServerMatch} from '../shared/servermatch';
import {Router} from '@angular/router';
import {DataService} from '../services/data.service';
import {HttpService} from '../services/http.service';
import {ProgressService} from '../services/progress.service';

@Component({
  selector: 'app-settings-page',
  templateUrl: './settings-page.component.html',
  styleUrls: ['./settings-page.component.css']
})
export class SettingsPageComponent implements OnInit {

  elements: any;

  constructor(private router: Router, private dataService: DataService,
    private httpSerice: HttpService, private progressService: ProgressService) {
    this.elements = {
      wifi: {
        title: 'Wifi',
        imageUrl: ServerMatch.STATIC + 'assets/images/wifi.png',
      },
      pincode: {
        title: 'Pin Code',
      },
      askBefore: {
        title: 'Ask Before',
        onFalse: ServerMatch.STATIC + 'assets/images/up.png',
        onTrue: ServerMatch.STATIC + 'assets/images/down.png',
        state: this.dataService.askBefore,
        imageUrl: ServerMatch.STATIC + 'assets/images/up.png',
      },
      lastPrints: {
        title: 'Last Prints',
        imageUrl: ServerMatch.STATIC + 'assets/images/print-logo.png',
      },
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
      },
    };
  }

  ngOnInit() {
  }

  askBeforeFunctionality() {
    // TODO: needs testing :/
    this.progressService.enable();
    this.httpSerice.post('abs', {abs: !this.elements.askBefore['state']}).subscribe(
      data => {
        this.progressService.disable();
        this.elements.askBefore['state'] = !this.elements.askBefore['state'];
        this.elements.askBefore['imageUrl'] = this.elements.askBefore[
          (this.elements.askBefore['state'] === true ? 'onTrue' : 'onFalse')
        ];
      }, err => {
        this.progressService.disable();
        console.warn('error in ask before funct', err);
      }
    );
  }

  wifiPage() {
    this.router.navigate([`/wifi`]);
  }

  goToLastPrintsPage() {
    this.router.navigate(['/last_prints']);
  }

  goBackToHomePage() {
    this.router.navigate([`/home`]);
  }

}



