import {Component, OnInit} from '@angular/core';
import {Router} from '../../../../node_modules/@angular/router';
import {HttpService} from '../../services/http.service';
import {DataSource} from '../../../../node_modules/@angular/cdk/collections';
import {BehaviorSubject} from '../../../../node_modules/rxjs/BehaviorSubject';
import {MatTableDataSource, MatSnackBar} from '../../../../node_modules/@angular/material';
import {ProgressService} from '../../services/progress.service';
import {DataService} from '../../services/data.service';
import {ServerMatch} from '../../shared/servermatch';
import {VariableService} from '../../services/variable.service';

interface PrintFiles {
  time: string;
  temperature: Number;
  file_name: String;
  filament_type: String;
  is_finished: string;
}

@Component({
  selector: 'app-last-prints',
  templateUrl: './last-prints.component.html',
  styleUrls: ['../../base-template/base-template.component.css',
    './last-prints.component.css']
})
export class LastPrintsComponent implements OnInit {
  displayedColumns = ['file_name', 'filament_type', 'time', 'temperature', 'is_finished'];
  dataSource = new MatTableDataSource();
  elements: any;

  constructor(private router: Router, private httpService: HttpService,
    private snackBar: MatSnackBar, private progressService: ProgressService,
    public dataService: DataService, public variableService: VariableService) {
    this.elements = {
      return: {
        imageUrl: ServerMatch.STATIC + 'assets/images/return.png',
        title: 'بازکشت',
      }
    };
  }

  ngOnInit() {
    // TODO: remove this initial mock data!
    this.dataSource.data = [{
      time: this.variableService.stringizedTime(37000),
      temperature: 180,
      file_name: 'batman arkham asylum 2000202020202flisjdflsdij lidfs',
      filament_type: 'PLA',
      is_finished: 'yes',
    }, {
      time: this.variableService.stringizedTime(304928),
      temperature: 205,
      file_name: 'batarang DKT',
      filament_type: 'Flexible',
      is_finished: 'no',
    },
    {}, {}, {}, {},
    {}, {}, {}, {}];
    this.dataSource.data = [];

    this.getLastPrintedFiles();
  }

  getLastPrintedFiles() {
    this.progressService.enable();
    this.httpService.get('last_prints', true).subscribe(
      data => {
        data.forEach(row => {
          row['time'] = this.variableService.stringizedTime(row['time']);
          if (!row.hasOwnProperty('filament_type'))
            row['filament_type'] = 'PLA Test';
          row['is_finished'] = (row['is_finished'] ? 'Yes' : 'No');
        });
        this.dataSource.data = data;
        this.progressService.disable();
      },
      err => {
        console.warn('could not get the files: ', err);
        this.snackBar.open('قادر به دریافت لیست نیست', null, {duration: 1600});
        this.progressService.disable();
      }
    );
  }

  goToSettingsPage() {
    this.router.navigate(['settings']);
  }

}


